.wrapper {
  overflow: hidden;
}
.btn-green {
  background: #84BB59;
  border-radius: 60px;
  &:hover {
    background: #6d9949;
  }
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 800;
    color: #fff;
    text-transform: uppercase;
  }
}
.btn-blue {
  background: #2AA2D6;
  border-radius: 60px;
  &:hover {
    background: #228cb8;
  }
  span {
    font-size: 16px;
    line-height: 120%;
    font-weight: 800;
    color: #fff;
    text-transform: uppercase;
  }
}
.nice-select {
  height: unset;
  width: auto;
  border: none;
  border-bottom: 1px solid #D9D9D9;
  border-radius: 0;
  padding: 0 24px 16px 0;
  &:after {
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    width: 16px;
    height: 10px;
    background-image: url("../img/select-arrow.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
  }
  .current {
    font-size: 16px;
    line-height: 120%;
    font-weight: 600;
    color: #000;
  }
  .list {
    background: #fff;
    border: 1px solid #D9D9D9;
    box-shadow: unset;
    border-radius: 0;
    margin-top: 8px;
    li {
      font-size: 16px;
      line-height: 120%;
      border-top: unset;
      padding: 10px;
      &:not(:last-child) {
        border-bottom: 1px solid #D9D9D9;
      }
    }
  }
}
.nice-select.open {
  &:after {
    transform: translate(0, -50%) rotate(-180deg);
  }
}

@media screen and (max-width: 992px) {
  .nice-select {
    padding-right: 16px;
    &:after {
      width: 12px;
      height: 7px;
    }
  }
}