.header {
  background: #fff;
  box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.05);
  padding: 24px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 24px;
  }
  &__text {
    font-size: 16px;
    line-height: 150%;
    a {
      font-weight: 800;
    }
  }
  &__profile {
    display: flex;
    align-items: center;
    gap: 10px;
    color: #575757;
    &:hover {
      color: #000;
      svg path {
        stroke: #000;
      }
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
    }
    &-icon {
      width: 24px;
      display: flex;
      align-items: center;
      svg {
        width: 100%;
        height: auto;
        path {
          transition: all 0.3s;
        }
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    padding: 16px 0;
    &__text {
      font-size: 14px;
      span {
        display: none;
      }
    }
    &__profile {
      &-text {
        display: none;
      }
    }
  }
}