.page-top {
  padding: 30px 0;
  &__items {
    display: grid;
    grid-template-columns: 1.13fr 0.87fr;
    gap: 30px;
  }
  &__item {
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding: 24px;
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 600;
    border-bottom: 1px solid #D9D9D9;
    padding-bottom: 24px;
  }
  &__flex {
    display: flex;
    justify-content: space-between;
    align-items: start;
    gap: 24px;
    padding-top: 24px;
  }
  &__desc {
    width: 53%;
    display: flex;
    gap: 24px;
  }
  &__btns {
    width: 47%;
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 24px / 2));
    gap: 24px;
    border-left: 1px solid #D9D9D9;
    padding-left: 24px;
  }
  &__group {
    min-width: 93px;
    display: flex;
    flex-direction: column;
    border-right: 1px solid #D9D9D9;
    padding-right: 24px;
    &:last-child {
      border-right: unset;
      padding-right: 0;
    }
  }
  &__subtitle {
    font-size: 13px;
    line-height: 120%;
    font-weight: 500;
    color: #575757;
    margin-bottom: auto;
  }
  &__info {
    font-size: 30px;
    line-height: 120%;
    font-weight: 800;
    white-space: nowrap;
    margin-top: 8px;
  }
  &__btn {
    height: 60px;
    padding: 4px 24px;
  }
}
.info {
  padding-bottom: 60px;
  &__inner {
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding: 0 24px 24px 24px;
  }
  &__tabs-head {
    display: flex;
    align-items: stretch;
    border-bottom: 1px solid #D9D9D9;
  }
  &__tab {
    position: relative;
    max-width: 213px;
    width: 100%;
    font-size: 16px;
    line-height: 112%;
    font-weight: 600;
    color: #7C7C7C;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    padding: 24px 8px;
    cursor: pointer;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4px;
      background: #2AA2D6;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      color: #000;
    }
  }
  .tab--active {
    color: #000;
    &:after {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  table {
    width: 100%;
    border-collapse: collapse;
    border: none;
    thead {
      tr {
        th {
          vertical-align: top !important;
          text-align: start;
          font-size: 13px;
          line-height: 120%;
          font-weight: 500;
          color: #575757;
          padding: 24px 5px;
          &:first-child {
            padding-left: 24px;
          }
          &:last-child {
            padding-right: 24px;
          }
        }
      }
    }
    tbody {
      tr {
        &:nth-child(odd) {
          background: #F4F7FA;
        }
        td {
          text-align: start;
          font-size: 16px;
          line-height: 120%;
          color: #000;
          padding: 24px 5px;
          &:first-child {
            padding-left: 24px;
          }
          &:last-child {
            padding-right: 24px;
          }
        }
      }
    }
  }
  .table2 {
    table-layout: fixed;
    thead {
      tr {
        th:first-child {
          width: 84px;
        }
      }
    }
  }
  .activated {
    font-weight: 600;
    color: #2AA2D6;
  }
  &__subtitle {
    font-size: 13px;
    line-height: 120%;
    font-weight: 500;
    color: #575757;
    margin-top: 24px;
  }
  &__select {
    display: block;
    margin-top: 16px;
  }
  .nice-select {
    max-width: 427px;
    width: 100%;
  }
  &__items {
    display: none;
  }
}
.sidebar {
  position: fixed;
  z-index: 10;
  top: 0;
  right: -105%;
  width: 100%;
  height: 100%;
  padding-left: 40px;
  transition: all 0.7s;
  &--active {
    right: 0;
  }
  &__inner {
    position: relative;
    z-index: 10;
    max-width: 464px;
    width: 100%;
    height: 100%;
    background: #fff;
    box-shadow: 0px 4px 10px 0px rgba(0, 0, 0, 0.05);
    padding: 32px 72px;
    margin-left: auto;
    overflow-y: auto;
    &::-webkit-scrollbar {
      width: 4px;
    }
    &::-webkit-scrollbar-track {
      background: #575757;
    }
    &::-webkit-scrollbar-thumb {
      background: #2AA2D6;
    }
  }
  &__close {
    width: 20px;
    margin-left: auto;
    cursor: pointer;
    &:hover {
      svg path {
        fill: #000;
      }
    }
    svg {
      width: 100%;
      height: auto;
      path {
        transition: all 0.3s;
      }
    }
  }
  &__title {
    font-size: 20px;
    line-height: 120%;
    font-weight: 700;
    margin-top: 32px;
  }
  &__subtitle {
    font-size: 13px;
    line-height: 120%;
    font-weight: 500;
    color: #575757;
    margin-top: 32px;
  }
  &__tabs-head {
    display: grid;
    grid-template-columns: 100%;
    gap: 16px;
    margin-top: 16px;
  }
  &__tab {
    position: relative;
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding: 24px 24px 24px 72px;
    cursor: pointer;
    transition: all 0.3s;
    &-title {
      font-size: 16px;
      line-height: 150%;
      font-weight: 600;
    }
    &-text {
      font-size: 16px;
      line-height: 150%;
      margin-top: 10px;
    }
    &:after {
      content: '';
      position: absolute;
      top: 24px;
      left: 24px;
      width: 24px;
      height: 24px;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 10px 8px;
      background-color: #fff;
      border: 1px solid rgba(87, 87, 87, 0.5);
      border-radius: 50%;
    }
  }
  .tab--active {
    &:after {
      background-image: url("../img/check.svg");
      background-color: #2AA2D6;
      border-color: transparent;
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 32px;
    margin-top: 32px;
    &-group {
      input {
        width: 100%;
        border: none;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        padding-bottom: 16px;
        font-size: 16px;
        line-height: 150%;
        color: #000;
        &::placeholder {
          opacity: 0.5;
        }
      }
    }
    &-btn {
      width: 100%;
      height: 60px;
      border-radius: 30px;
      padding: 8px;
    }
  }
  .input-title {
    display: block;
    font-size: 13px;
    line-height: 120%;
    font-weight: 500;
    color: #575757;
    margin-bottom: 16px;
  }
  &__price-wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 4px;
    font-size: 16px;
    line-height: 150%;
    font-weight: 600;
  }
  .nice-select {
    width: 100%;
    .current {
      font-weight: 400;
    }
  }
  &-bg {
    position: fixed;
    z-index: 5;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.25);
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s;
    &--active {
      opacity: 1;
    }
  }
}

@media screen and (max-width: 1500px) {
  .page-top {
    &__info {
      font-size: 24px;
    }
    &__btn {
      span {
        font-size: 14px !important;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .page-top {
    &__item {
      display: flex;
      flex-direction: column;
    }
    &__flex {
      flex-grow: 1;
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__btns {
      width: 100%;
      border: unset;
      padding: 0;
      margin-top: auto;
    }
    &__btn {
      width: 100%;
    }
  }
}

@media screen and (max-width: 1300px) {
  .page-top {
    &__item {
      display: flex;
      flex-direction: column;
    }
    &__flex {
      flex-grow: 1;
      flex-direction: column;
    }
    &__desc {
      width: 100%;
    }
    &__btns {
      width: 100%;
      border: unset;
      padding: 0;
      margin-top: auto;
    }
    &__btn {
      width: 100%;
    }
  }
  .info {
    &__table {
      width: 100%;
      overflow-x: auto;
      &::-webkit-scrollbar {
        height: 4px;
      }
      &::-webkit-scrollbar-track {
        background: #575757;
      }
      &::-webkit-scrollbar-thumb {
        background: #2AA2D6;
      }
    }
    table {
      width: 1190px;
    }
  }
}

@media screen and (max-width: 992px) {
  .page-top {
    padding: 20px 0;
    &__items {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
    &__item {
      padding: 16px;
    }
    &__title {
      font-size: 16px;
      padding-bottom: 16px;
    }
    &__flex {
      padding-top: 16px;
    }
    &__btns {
      grid-template-columns: repeat(2, calc(50% - 16px / 2));
      gap: 16px;
    }
    &__btn {
      height: 48px;
    }
  }
  .info {
    padding-bottom: 40px;
    &__inner {
      padding: 0 16px 16px 16px;
    }
    &__tab {
      padding: 16px 4px;
    }
    &__table {
      display: none;
    }
    &__subtitle {
      margin-top: 16px;
    }
    &__items {
      width: 100%;
      display: grid;
      grid-template-columns: 100%;
      padding-top: 16px;
    }
    &__item {
      &:nth-child(odd) {
        background: #F4F7FA;
      }
      &-show {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 8px;
        padding: 16px 16px 10px;
      }
      .accordion-show--active {
        .info__item-btn {
          transform: rotate(-180deg);
        }
      }
      &-title {
        font-size: 16px;
        line-height: 120%;
        font-weight: 500;
      }
      &-btn {
        width: 12px;
        flex-shrink: 0;
        transition: all 0.3s;
        img {
          width: 100%;
        }
      }
      &-hidden {
        display: none;
        padding: 0 16px 16px 16px;
      }
      &-list {
        display: grid;
        grid-template-columns: 100%;
        gap: 10px;
        li {
          display: grid;
          grid-template-columns: 100%;
          gap: 10px;
          span:nth-child(1) {
            font-size: 13px;
            line-height: 120%;
            font-weight: 500;
            color: #575757;
          }
          span:nth-child(2) {
            font-size: 16px;
            line-height: 120%;
          }
        }
      }
    }
  }
  .sidebar {
    &__inner {
      padding: 20px;
    }
    &__close {
      position: absolute;
      top: 20px;
      right: 20px;
    }
    &__title {
      font-size: 16px;
      padding-right: 24px;
      margin-top: 0;
    }
    &__subtitle {
      margin-top: 20px;
    }
    &__tabs-head {
      gap: 10px;
    }
    &__tab {
      padding: 16px 16px 16px 50px;
      &:after {
        top: 16px;
        left: 16px;
      }
      &-text {
        font-size: 14px;
        margin-top: 6px;
      }
    }
    &__form {
      gap: 20px;
      margin-top: 20px;
      &-btn {
        height: 48px;
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .page-top {
    &__items {
      grid-template-columns: 100%;
    }
    &__info {
      font-size: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .page-top {
    &__group {
      min-width: auto;
      flex-basis: 0;
      flex-grow: 1;
    }
  }
}

@media screen and (max-width: 359px) {
  .page-top {
    &__subtitle {
      font-size: 12px;
    }
    &__info {
      font-size: 24px;
    }
    &__btn {
      span {
        font-size: 13px !important;
      }
    }
  }
}