.registration {
  padding: 250px 0;
  &__inner {
    max-width: 370px;
    width: 100%;
    margin: auto;
  }
  &__logo {
    width: 215px;
    display: block;
    margin: 0 auto;
    img {
      width: 100%;
    }
  }
  &__wrap {
    background: #fff;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 16px;
    padding: 0 24px 24px 24px;
    margin-top: 60px;
  }
  &__tabs-head {
    display: grid;
    grid-template-columns: repeat(2, 50%);
    border-bottom: 1px solid #D9D9D9;
  }
  &__tab {
    position: relative;
    font-size: 16px;
    line-height: 112%;
    font-weight: 600;
    color: #7C7C7C;
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    padding: 24px 8px;
    cursor: pointer;
    transition: all 0.3s;
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 4px;
      background: #2AA2D6;
      opacity: 0;
      pointer-events: none;
      transition: all 0.3s;
    }
    &:hover {
      color: #000;
    }
  }
  .tab--active {
    color: #000;
    &:after {
      opacity: 1;
      pointer-events: auto;
    }
  }
  .tab__content {
    display: none;
    &--active {
      display: block;
    }
  }
  &__form {
    display: grid;
    grid-template-columns: 100%;
    gap: 24px;
    margin-top: 24px;
    &-group {
      position: relative;
      input {
        width: 100%;
        height: 60px;
        background: #F3F3F3;
        border: none;
        border-radius: 60px;
        padding: 0 58px 0 24px;
        font-size: 16px;
        line-height: 120%;
        color: #000;
        &::placeholder {
          color: #575757 !important;
        }
      }
      .input-icon {
        position: absolute;
        top: 50%;
        right: 24px;
        transform: translate(0, -50%);
        max-width: 24px;
        pointer-events: none;
        img {
          max-width: 100%;
        }
      }
    }
    &-btn {
      width: 100%;
      height: 60px;
    }
    &-text {
      text-align: center;
      font-size: 15px;
      line-height: 120%;
      color: rgba(0, 0, 0, 0.7);
    }
    &-socials {
      display: flex;
      justify-content: center;
      gap: 24px;
    }
    &-social {
      width: 60px;
      height: 60px;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #D9D9D9;
      border-radius: 50%;
      img {
        max-width: 24px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .registration {
    padding: 80px 0;
  }
}